.home-page {
    min-height: 100vh;
    width: 100%;
   
    .empty-msg{
        font-size: 50px;
        max-width: 100%;
        font-weight: 900;
        color: rgba(7, 7, 7, 0.452);
    }
    .app-center{
        height: 100%;
    }
}

.fade-enter{
    opacity: 0;
    z-index: 1;
}

.fade-enter.fade-enter-active{
    opacity: 0.5;
    transition: opacity 200ms ease-in;
}
